.rt-card {
  position: relative;
  z-index: 0;
  overflow: hidden;
}
.rt-card .icon {
  margin-bottom: 25px;
  position: relative;
  overflow: hidden;
  transition: all 0.4s ease-in-out;
}
.rt-card .card-title {
  margin-bottom: 19px;
  line-height: 28px;
}
.rt-card .card-title a {
  font-family: var(--font-roboto);
  font-weight: 600;
  font-size: 24px;
  color: var(--color-title);
  transition: all 0.4s ease-in-out;
}
.rt-card .card-title a:hover {
  color: #1D2746;
}
.rt-card .card-info {
  font-family: var(--font-roboto);
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  color: #6B707F;
  margin-bottom: 24px;
  transition: all 0.4s ease-in-out;
}
.rt-card .card-btn {
  font-family: var(--font-roboto);
  font-weight: 500;
  font-size: 15px;
  line-height: 26px;
  color: #1D2746;
  border: 1px solid #1D2746;
  border-radius: 100px;
  padding: 6px 21px;
  transition: all 0.4s ease-in-out;
}
.rt-card--style-1 {
