.forum-page-section {
    overflow: unset;
    .forum-page-content-wrapper {
        margin-bottom: 80px;
    }
}
.forum-widget-wrapper {
    background: $color-white;
    box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.05);
    border-radius: 20px;
    padding: 38px 44px 68px 42px;

    .title {
        font-family: $font-roboto;
        font-weight: 600;
        font-size: 24px;
        line-height: 28px;
        color: $color-title;
        margin-bottom: 20px;
    }
}